import React from 'react';
import '../assets/css/Cuerpo.css';


//IMPORTACION IMAGENES
import Yuji from '../assets/img/YujiItadori.png'
import manga from '../assets/img/escenaManga.jpg'
import pjAnime from '../assets/img/personajesManga.png'


//IMPORTACION DE SECCIONES
import Img from './Img'
import SectionTexto1Foto from './SectionTexto1Foto'


//JAVASCRIPT
//EVENTO IMAGEN SALIENDO DE LA DERECHA


//EVENTO IMAGEN LOCAL AMPLIANDO SU TAMAÑO Y SALIENDO ITACHI
function eventoMouseOver(){
    
    var resolucion = window.innerWidth;
    var PersonajeAnime = document.getElementById('personajeAnime')
    var imagenAnimada = document.getElementById('imagenSeccion3Local')

    if(resolucion <= 500){
       
        PersonajeAnime.style.opacity='1'
        PersonajeAnime.style.width='100vw'
        PersonajeAnime.style.height='100vw'
        PersonajeAnime.style.marginTop='-100%'
       
        
        imagenAnimada.style.filter= 'brightness(50%)'
        imagenAnimada.style.width='100%'
        imagenAnimada.style.height='75vw'
        imagenAnimada.style.margin='12% 0% 0% 0%'  

    }
    else{
        
        if(resolucion > 500 && resolucion <= 1100){
            PersonajeAnime.style.opacity='1'
            PersonajeAnime.style.width='100vw'
            PersonajeAnime.style.height='100vw'
            PersonajeAnime.style.marginTop='-100%'
            
            imagenAnimada.style.filter= 'brightness(50%)'
            imagenAnimada.style.width='100%'
            imagenAnimada.style.height='90vw'
            imagenAnimada.style.margin='8% 0% 0% 0%' 

        }
        if(resolucion > 1100 && resolucion < 3000){

            PersonajeAnime.style.opacity='1'
            PersonajeAnime.style.width='45vw'
            PersonajeAnime.style.height='38vw'
            PersonajeAnime.style.marginLeft='0vw'
            PersonajeAnime.style.marginTop='-120%'
            

            imagenAnimada.style.filter= 'brightness(50%)'
            imagenAnimada.style.width='100%'
            imagenAnimada.style.height='70vh'
            imagenAnimada.style.margin='18% 2% 0% 2%' 
            
        }
         
    }
    
}

//EVENTO ACHICAR IMAGEN manga + Yuji ESCONDIENDOSE    
function eventoMouseOut()
{
    var resolucion = window.innerWidth;
    var PersonajeAnime = document.getElementById('personajeAnime')
    var imagenAnimada = document.getElementById('imagenSeccion3Local')
   

    if(resolucion <= 500){
        PersonajeAnime.style.opacity='0'
        PersonajeAnime.style.width='0vw'
        PersonajeAnime.style.marginTop='0%'
        
        imagenAnimada.style.filter= 'brightness(100%)'
        imagenAnimada.style.width='90%'
        imagenAnimada.style.height='60vw'
        imagenAnimada.style.margin='8vw 0% 0% 4.5%'  
    }
    else{
        if(resolucion > 500 && resolucion <= 1100){
        PersonajeAnime.style.opacity='0'
        PersonajeAnime.style.width='0vw'
        PersonajeAnime.style.marginTop='0%'
        
        imagenAnimada.style.filter= 'brightness(100%)'
        imagenAnimada.style.width='90%'
        imagenAnimada.style.height='80vw'
        imagenAnimada.style.margin='5% 0% 0% 5%'  
        }

        if(resolucion > 1100 && resolucion < 3000){

            PersonajeAnime.style.opacity='0'
            PersonajeAnime.style.width='0vw'
            PersonajeAnime.style.marginLeft='0vw'
            PersonajeAnime.style.marginTop='-30%'
            

            imagenAnimada.style.filter= 'brightness(100%)'
            imagenAnimada.style.width='80%'
            imagenAnimada.style.height='55vh'
            imagenAnimada.style.margin='28% 5% 0% 2%' 
            
        }
       
    }
       
}
